/* Design tokens for consistent theming */
:root {
  /* Light mode colors (HSL for better manipulation) */
  --bg: 210 40% 98%;           /* slate-50 equivalent */
  --surface: 0 0% 100%;        /* pure white */
  --text: 222 47% 11%;         /* slate-900 equivalent */
  --muted: 215 20% 35%;        /* slate-600 equivalent */
  --border: 214 32% 91%;       /* slate-200 equivalent */
  --accent: 245 58% 51%;       /* indigo-500 */
  --accent-2: 262 83% 58%;     /* violet-500 */
  --ring: 236 83% 75%;         /* focus ring color */
  --shadow: 220 40% 2%;        /* shadow color base */
  
  /* Semantic colors */
  --success: 142 76% 36%;      /* emerald-500 */
  --info: 199 89% 48%;         /* sky-500 */
  --warning: 38 92% 50%;       /* amber-500 */
  --danger: 0 84% 60%;         /* red-500 */
  
  /* Design tokens */
  --radius: 1rem;
  --radius-sm: 0.5rem;
  --radius-lg: 1.5rem;
  
  /* Typography scale (fluid) */
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-lg: 1.125rem;
  --text-xl: 1.25rem;
  --text-2xl: 1.5rem;
  --text-3xl: 1.875rem;
  --text-4xl: 2.25rem;
  --text-5xl: 3rem;
  --text-6xl: 3.75rem;
  
  /* Spacing scale */
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;
  
  /* Animation timing */
  --duration-fast: 150ms;
  --duration-normal: 200ms;
  --duration-slow: 300ms;
  --ease-out: cubic-bezier(0, 0, 0.2, 1);
  --ease-in: cubic-bezier(0.4, 0, 1, 1);
  --ease-bounce: cubic-bezier(0.2, 0.8, 0.2, 1);
  
  /* Z-index scale */
  --z-nav: 50;
  --z-overlay: 90;
  --z-panel: 9999;
  --z-tooltip: 110;
}

/* Dark mode overrides */
.dark {
  --bg: 222 47% 11%;           /* slate-900 equivalent */
  --surface: 222 47% 13%;      /* slightly lighter surface */
  --text: 210 40% 98%;         /* slate-50 equivalent */
  --muted: 215 20% 65%;        /* slate-400 equivalent */
  --border: 217 19% 27%;       /* slate-700 equivalent */
  --accent: 245 58% 60%;       /* lighter indigo in dark */
  --accent-2: 262 83% 66%;     /* lighter violet in dark */
  --ring: 236 83% 75%;         /* same focus ring */
  --shadow: 220 40% 2%;        /* deeper shadow in dark */
  
  /* Adjusted semantic colors for dark mode */
  --success: 142 76% 46%;      /* emerald-400 */
  --info: 199 89% 58%;         /* sky-400 */
  --warning: 38 92% 60%;       /* amber-400 */
  --danger: 0 84% 70%;         /* red-400 */
}

/* Base styles with fallbacks */
html, body, #root {
  background-color: hsl(var(--bg));
  color: hsl(var(--text));
  transition: background-color var(--duration-normal) var(--ease-out),
              color var(--duration-normal) var(--ease-out);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, 
               'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
}

/* Scroll behavior improvements */
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus-visible styles */
:focus-visible {
  outline: 2px solid hsl(var(--ring));
  outline-offset: 2px;
}

/* Selection styles */
::selection {
  background-color: hsl(var(--accent) / 0.2);
  color: hsl(var(--text));
}

::-moz-selection {
  background-color: hsl(var(--accent) / 0.2);
  color: hsl(var(--text));
}

/* Utility classes for consistent animations */
.animate-fade-in {
  animation: fade-in var(--duration-slow) var(--ease-out);
}

.animate-slide-up {
  animation: slide-up var(--duration-slow) var(--ease-out);
}

.animate-slide-in-left {
  animation: slide-in-left var(--duration-slow) var(--ease-out);
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slide-in-left {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Custom scrollbar for webkit browsers */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: hsl(var(--bg));
}

::-webkit-scrollbar-thumb {
  background: hsl(var(--muted) / 0.3);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted) / 0.5);
}

/* Mobile-specific improvements */
@media (max-width: 768px) {
  /* Ensure minimum touch targets on mobile */
  button, a, input, select, textarea {
    min-height: 44px;
    min-width: 44px;
  }
  
  /* Improve button spacing on mobile */
  button:not([aria-expanded="true"]) + button {
    margin-left: 8px;
  }
  
  /* Better text inputs on mobile */
  input, textarea, select {
    font-size: 16px; /* Prevents zoom on iOS */
    border-radius: var(--radius-sm);
  }
  
  /* Prevent horizontal scroll */
  body {
    overflow-x: hidden;
  }
}

/* Touch-friendly focus states */
@media (hover: none) and (pointer: coarse) {
  /* Touch device specific styles */
  button:active, 
  a:active,
  [role="button"]:active {
    background-color: hsl(var(--accent) / 0.1);
    transform: scale(0.98);
  }
}

/* Skip link for accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: hsl(var(--accent));
  color: white;
  padding: 8px;
  text-decoration: none;
  border-radius: var(--radius-sm);
  z-index: 1000;
  transition: top var(--duration-fast) var(--ease-out);
}

.skip-link:focus {
  top: 6px;
}